export default {
	config: {
		baseUrl: configs.baseUrl,
		header: {
			'Content-Type':'application/json;charset=UTF-8',  // content-Type  application/json
			// 'Content-Type':'application/x-www-form-urlencoded'
		},  
		data: {},
		loading: false,
		loadingText: '',
		method: "POST",  //  method
		dataType: "json",  /* json JSON.parse */
		responseType: "text",
		success() {},
		fail() {},
		complete() {}
	},
	// 
	interceptor: {
		// 
		request: (configs) => {
		  //   token
		//   debugger
		  const data = configs.data || {}
		  // loading
		  const loading = configs.loading 
		  if( configs.url === `${configs.baseUrl}/users/register` ){
			  
		  }else {
			//    token 
			configs.data = JSON.stringify(Object.assign(configs.data, {
				'token': store.getters.userToken  // store  userToken 
			}))  
		  }
		  //  loading 
		  if(loading){
			const title = configs.loadingText
			// loading
			uni.showLoading({
				title: title,
				mask: true
			});
		  }
		},
		// 
		response: (configs) => {
			// debugger
			// loading
			configs.config.loading && uni.hideLoading()
			return configs
		}
	},
	request(options) {
		// debugger
		if (!options) {
			options = {}
		}
		options.header = options.header || this.config.header
		options.baseUrl = options.baseUrl || this.config.baseUrl
		options.dataType = options.dataType || this.config.dataType
		options.url = options.baseUrl + options.url
		options.data = options.data || {}
		options.loading = options.loading || this.config.loading
		options.loadingText = options.loadingText || this.config.loadingTextloadingText
		options.method = options.method || this.config.method
		
		//TODO 
		
		//TODO 
		/* 
		_token = {'token': getStorage(STOREKEY_LOGIN).token || 'undefined'},
		_sign = {'sign': sign(JSON.stringify(options.data))}
		options.header = Object.assign({}, options.header, _token,_sign) 
		*/
	   
		return new Promise((resolve, reject) => {
			// debugger
			let _config = null
			
			//options.complerte  uni.request()  uni.request() 
			//uni.request()response options.complate
			options.complete = (response) => {
				// uni.require()  complete 
				console.log("" + new Date().getTime() + ":uni.require() complete")
				// debugger
				// uni.require()    statusCode 
				let statusCode = response.statusCode
				
				response.config = _config
				
				if (process.env.NODE_ENV === 'development') {
					// debugger
					// 
					if (statusCode === 200) {
						console.log("" + _config.requestId + " " + JSON.stringify(response.data))
					}
				}
				
				//    
				if (this.interceptor.response) {
					// debugger
					let newResponse = this.interceptor.response(response)
					if (newResponse) {
						response = newResponse
					}
				}
				// 
				_reslog(response)
        
				if (statusCode === 200) { //
					// debugger
					resolve(response)
				} else {
					reject(response)
				}
			}
			
			options.fail = (error) => {
				// uni.require()  fail 
				console.log("" + new Date().getTime() + ":uni.require() fail")
			}
			
			options.success = (response) => {
				// uni.require()  success 
				console.log("" + new Date().getTime() + ":uni.require() success")
			}
			// debugger
			//   _config
			_config = Object.assign({}, this.config, options)
			_config.requestId = new Date().getTime()
			
			// , 
			if (this.interceptor.request) {
				this.interceptor.request(_config)
			}
			
			// 
			_reqlog(_config)
			
			if (process.env.NODE_ENV === 'development') {
				//  
				console.log("" + _config.requestId + " " + _config.url)
				if (_config.data) {
					console.log("" + _config.requestId + " " + JSON.stringify(_config.data))
				}
			}

			//  uni.request 
			// debugger
			let a = uni.request(_config);
			// debugger
		});
	},
	// get 
	get(url, data, options) {
		// debugger
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'GET'  
		return this.request(options)
	},
	// post 
	post(url, data, options) {
		// debugger
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'POST'
		return this.request(options)
	},
	// put
	put(url, data, options) {
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'PUT'
		return this.request(options)
	},
	// delete 
	delete(url, data, options) {
		if (!options) {
			options = {}
		}
		options.url = url
		options.data = data
		options.method = 'DELETE'
		return this.request(options)
	}
}


/**
 * 
 */
function _reqlog(req) {
	if (process.env.NODE_ENV === 'development') {
		console.log("" + req.requestId + " " + req.url)
		if (req.data) {
			// debugger
			console.log("" + req.requestId + " " + JSON.stringify(req.data))
			// debugger
		}
	}
	//TODO 
}

/**
 * 
 */
function _reslog(res) {
	let _statusCode = res.statusCode;
	if (process.env.NODE_ENV === 'development') {
		console.log("" + res.config.requestId + " " + res.config.url)
		if (res.config.data) {
			console.log("" + res.config.requestId + " " + JSON.stringify(res.config.data))
		}
		console.log("" + res.config.requestId + " " + JSON.stringify(res))
	}
	//TODO 
	switch(_statusCode){
		case 200:
			break;
		case 401:
			break;
		case 404:
			break;
		default:
			break;
	}
}


