		// loading
		showLoading(title, duration = 2000) {
			this.getDeviceApi().showLoading({
				title: title || '',
			})
			if (duration > 0) {
				return new Promise((resolve, reject) => {
					setTimeout(() => {
						resolve();
					}, duration);
				});
			}
		},
		//  loading
		hideLoading(duration = 2000 ){
			this.getDeviceApi().hideLoading()
			if (duration > 0) {
				return new Promise((resolve, reject) => {
					setTimeout(() => {
						resolve();
					}, duration);
				});
			}			
		},
		//  showToast
		success(title, duration = 2000, mask) {
			this.getDeviceApi().showToast({
				title: title,
				icon: 'success',
				mask: mask ? false : true,
				duration: duration
			});
			if (duration > 0) {
				return new Promise((resolve, reject) => {
					setTimeout(() => {
						resolve();
					}, duration);
				});
			}
		},
		// modal
		modal(text, title = '') {
			return new Promise((resolve, reject) => {
				this.getDeviceApi().showModal({
					title: title,
					content: text,
					showCancel: false,
					success: res => {
						resolve(res);
					},
					fail: res => {
						reject(res);
					}
				});
			});
		},
		// confirm
		confirm(text, payload = {}, title = '', confirmText = '', cancelText = '') {
			return new Promise((resolve, reject) => {
				let params = {
					title: title,
					content: text,
					showCancel: true,
					cancelText: cancelText,
					confirmText: confirmText,
					success: res => {
						if (res.confirm) {
							resolve(0, payload);
						} else if (res.cancel) {
							reject(1, payload);
						}
					},
					fail: res => {
						reject(-1, payload);
					}
				};
				this.getDeviceApi().showModal(params);
			});
		},
		// toast 
		toast(title, duration = 2000, onHide, icon = 'none') {
			this.getDeviceApi().showToast({
				title: title,
				mask: false,
				duration: duration,
				icon: icon
			});
			// 
			if (onHide) {
				setTimeout( () => {
					onHide();
				}, duration );
			}
		},
		// alert 
		alert(title, duration = 2000) {
			let _this = this;
			this.getDeviceApi().showToast({
				title: title,
				image: '../static/imgs/icon/alert.png',
				mask: false,
				duration: duration
			});
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve();
				}, duration);
			});
		},
		// error 
		error(title, duration = 2000, onHide) {
			let _this = this;
			this.getDeviceApi().showToast({
				title: title,
				image: '../static/imgs/icon/error.png',
				mask: true,
				duration: duration
			});
			// 
			if (onHide) {
				setTimeout(() => {
					onHide();
				}, duration);
			}
		},
