		// 
		switchTimeForMart( value ) {
			if( value ) {
				 let theTime = parseInt(value); 
				 let middle = 0; // 
				 let hour = 0; //
				 middle= parseInt(theTime/60);
				 theTime = parseInt(theTime%60);
				 if( theTime<10 ) {
					 theTime = "0"+ theTime ;
				 }

				 if(middle> 60) {
					 hour= parseInt(middle/60);
					 middle= parseInt(middle%60);
				 }

				 if( middle<10 ) {
					 middle = "0"+middle + ":";
				 }

				 if( hour<10 ){
					 if( hour ==0 ) {
						 hour = "";
					 }else {
						 hour = "0"+hour + ":";
					 }
				 }
				 var result = ""+hour + middle  + theTime;
				 return result;           
			} 
		},
